<template>
  <div class="home-container">
    <div class="input-container">
      <button v-show="this.notes.length > 0" class="write-button" @click="isShow = !isShow">{{isShow ? "취소" : "편집"}}</button>
      <button v-if="!showInputs" class="write-button" @click="showInputs = true">글쓰기</button>
      <div v-else class="input-form">
        <input class="input-title" v-model="title" placeholder="제목을 입력하세요" />
        <textarea class="input-content" v-model="content" placeholder="내용을 입력하세요"></textarea>
        <div class="button-container">
          <button class="save-button" @click="saveToIndexedDB">저장</button>
          <button class="cancel-button" @click="showInputs = false">취소</button>
        </div>
      </div>
    </div>
    
    <div class="notes-list">
      <div v-for="(note, index) in notes" :key="note.id" class="note-item">
        <div class="note-header">
          <div class="note-date">{{ formatDate(note.timestamp) }}</div>
          <button class="delete-button" v-if="isShow" @click="deleteNote(note.id)">삭제</button>
        </div>
        <div class="note-content">
          <h3 class="note-title">{{ note.title }}</h3>
          <p class="note-text">{{ note.content }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showInputs: false,
      title: '',
      content: '',
      notes: [],
      isShow : false,
      dbName : "data"
    };
  },
  created() {
    // this.loadNotesFromIndexedDB();
  },
  methods: {
    saveToIndexedDB() {
      // if (!this.title || !this.content) {
      //   alert('제목과 내용을 입력해주세요.');
      //   return;
      // }
      // debugger
      const request = indexedDB.open( `${this.dbName}`, 102);

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains('notes')) {
        const objectStore = db.createObjectStore('notes', { keyPath: 'id', autoIncrement: true });
        objectStore.createIndex('title', 'title', { unique: false }); // 변경: 중복 허용
        objectStore.createIndex('content', 'content', { unique: false }); // 변경: 중복 허용
    }
};

        request.onsuccess = (event) => {
            const db = event.target.result;
            const transaction = db.transaction(['notes'], 'readwrite');
            const objectStore = transaction.objectStore('notes');
        const newNote = { title: this.title, content: this.content }; 
        objectStore.add(newNote);
        transaction.oncomplete = () => {
          console.log('Note added to IndexedDB');
          this.title = '';
          this.content = '';
          this.showInputs = false; // Hide inputs after saving
        };
        transaction.onerror = (error) => {
          console.error('Error adding note:', error);
        };
        transaction.oncomplete = () => {
        console.log('Note added to IndexedDB');
        this.title = '';
        this.content = '';
        this.showInputs = false;

        // After saving, reload notes
        this.loadNotesFromIndexedDB();
       };
      };

      request.onerror = (error) => {
        console.error('Error opening database:', error);
      };
    },
    deleteNote(id) {
      const request = indexedDB.open(`${this.dbName}`, 102);

      request.onsuccess = (event) => {
        const db = event.target.result;
        const transaction = db.transaction(['notes'], 'readwrite');
        const objectStore = transaction.objectStore('notes');
        const deleteRequest = objectStore.delete(id);

        deleteRequest.onsuccess = () => {
          console.log('Note deleted from IndexedDB');
          this.loadNotesFromIndexedDB(); // Reload notes after deletion
        };

        deleteRequest.onerror = (error) => {
          console.error('Error deleting note:', error);
        };
      };

      request.onerror = (error) => {
        console.error('Error opening database:', error);
      };
    },
    loadNotesFromIndexedDB() {
      const request = indexedDB.open(`${this.dbName}`, 102);

      request.onsuccess = (event) => {
        const db = event.target.result;
        const transaction = db.transaction(['notes'], 'readonly');
        const objectStore = transaction.objectStore('notes');
        const getAllRequest = objectStore.getAll();

        getAllRequest.onsuccess = (event) => {
          this.notes = event.target.result;
        };

        transaction.onerror = (error) => {
          console.error('Error loading notes:', error);
        };
      };

      request.onerror = (error) => {
        console.error('Error opening database:', error);
      };
    },
    formatDate(timestamp) {
      if (!timestamp) {
        return '';
      }

      const date = new Date(timestamp);
      if (isNaN(date)) {
        return '';
      }

      const year = date.getFullYear();
      const month = (date.getMonth() + 1).toString().padStart(2, '0');
      const day = date.getDate().toString().padStart(2, '0');
      const hours = date.getHours().toString().padStart(2, '0');
      const minutes = date.getMinutes().toString().padStart(2, '0');

      return `${year}-${month}-${day} ${hours}:${minutes}`;
    }
  }
};
</script>

<style scoped>
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.notes-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  width: 100%;
}

.note-header {
  display: flex;
  justify-content: flex-end;
  color: #888;
  font-size: 12px;
  margin-bottom: 5px;
}

.note-date {
  white-space: nowrap;
}
.delete-button {
  position: absolute;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 12px;
}

.delete-button:hover {
  background-color: #c82333;
}

.note-item {
  position: relative;
  width:328px;
  height:auto;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.note-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.note-content {
  text-align: left;
}

.note-title {
  font-size: 18px;
  margin-bottom: 10px;
}

.note-text {
  font-size: 14px;
  color: #555;
  margin-bottom: 0;
}
.input-container {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.write-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.write-button:hover {
  background-color: #0056b3;
}

.input-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 4px;
  margin-top: 20px;
}

.input-title,
.input-content {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.input-title {
  font-weight: bold;
}

.input-content {
  height: 150px;
  resize: none;
}

.button-container {
  margin-top: 20px;
}

.save-button,
.cancel-button {
  display: inline-block;
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.save-button {
  background-color: #28a745;
  margin-right: 10px;
}

.save-button:hover {
  background-color: #218838;
}

.cancel-button {
  background-color: #dc3545;
}

.cancel-button:hover {
  background-color: #c82333;
}
</style>
